import React from "react";

// talons
import { useUserForm } from "../../../talons/User/useUserForm";

// utils
import mergeClasses from "../../../utils/mergeClasses";

// components
import { Form, Input, InputNumber, Select } from "antd";

// types
import { FORM_TYPE } from "../../../types/app.types";

// styles
import defaultClasses from "./userForm.module.css";

interface Props {
    classes?: object;
    view: FORM_TYPE;
}

const UserForm = ({ classes: propsClasses, view }: Props) => {
    const classes = mergeClasses(defaultClasses, propsClasses);

    const {
        form,
        type,
        user,
        handleCancel,
        onSubmit,
        onChange,
        handleChangeType,
    } = useUserForm({
        view,
    });

    let title = null;
    let buttonText = null;

    switch (view) {
        case "ADD":
            title = "Thm thng tin ti khon";
            buttonText = "Thm";
            break;
        case "EDIT":
            title = "Sa thng tin ti khon";
            buttonText = "Lu thng tin";
            break;
    }

    console.log(`type`, type);

    return (
        <div className={classes.root}>
            <h2 className={classes.title}>{title}</h2>

            <Form
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 14 }}
                layout="horizontal"
                onFinish={onSubmit}
                onValuesChange={onChange}
                form={form}
                initialValues={user || {}}
                key={Math.random()}
            >
                <Form.Item
                    label="Tn ngi dng"
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp tn ngi dng",
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="Tn ti khon (username)"
                    name="username"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp tn ti khon",
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                {view === "ADD" && (
                    <React.Fragment>
                        <Form.Item
                            label="Mt khu"
                            name="password"
                            rules={[
                                {
                                    required: true,
                                    message: "Xin hy nhp mt khu ti khon",
                                },
                            ]}
                        >
                            <Input.Password />
                        </Form.Item>
                        <Form.Item
                            label="Xc nhn mt khu"
                            name="passwordConfirm"
                            rules={[
                                {
                                    required: true,
                                    message: "Xin hy xc nhn mt khu",
                                },
                            ]}
                        >
                            <Input.Password />
                        </Form.Item>
                    </React.Fragment>
                )}
                <Form.Item
                    label="Email"
                    name="email"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp email",
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="S in thoi"
                    name="phoneNumber"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp s in thoi",
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item label="Quyn" name="role">
                    <Select
                        disabled={view !== "ADD"}
                        defaultValue={type}
                        onChange={handleChangeType}
                    >
                        <Select.Option value="staff">Nhn vin</Select.Option>
                        <Select.Option value="manager">Qun l</Select.Option>
                    </Select>
                </Form.Item>
                {(type === "staff" && (
                    <Form.Item
                        label="Phng ban"
                        name="department"
                        rules={[
                            {
                                required: true,
                                message: "Xin hy nhp phng ban",
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                )) || (
                    <Form.Item
                        label="V tr cng tc"
                        name="position"
                        rules={[
                            {
                                required: true,
                                message: "Xin hxay nhp v tr cng tc",
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                )}
                <div className={classes.btnGroup}>
                    <button type="submit" className={classes.btn}>
                        {buttonText}
                    </button>
                    <button className={classes.btn} onClick={handleCancel}>
                        Hy
                    </button>
                </div>
            </Form>
        </div>
    );
};

export default UserForm;
