import { useEffect, useState } from "react";

/**
 * A [React Hook]{@link https://reactjs.org/docs/hooks-intro.html}
 * Hm gip qun l data t local storage
 * @kind function.
 *
 *
 * @return {{
 * storedValue: any,
 * setStoredValue: func
 * }}
 *
 * */

const useLocalStorage = (key: string, initialValue: any) => {
    const [storedValue, setStoredValue] = useState(() => {
        try {
            // Ly ra gi tr t local storage theo key truyn vo
            const item = window.localStorage.getItem(key);

            // COnvert d liu
            return item ? JSON.parse(item) : initialValue;
        } catch (error) {
            // Nu khng tn ti object vi thng key truyn vo th tr v gi tr ban u truyn vo
            console.log(error);
            return initialValue;
        }
    });

    // Hm  set gi tr mi cho object trong localstorage
    const setValue = (value: any) => {
        try {
            // Allow value to be a function so we have same API as useState
            const valueToStore =
                value instanceof Function ? value(storedValue) : value;
            // Save state
            setStoredValue(valueToStore);
            // Save to local storage
            window.localStorage.setItem(key, JSON.stringify(valueToStore));
        } catch (error) {
            // A more advanced implementation would handle the error case
            console.log(error);
        }
    };

    return [storedValue, setValue];
};

export { useLocalStorage };
