import React, { useRef } from "react";

// talons
import { useGenerateReport } from "../../../talons/Report/useGenerateReport";

// components
import { Form, DatePicker, Button, Table, Empty } from "antd";
import ReactToPrint from "react-to-print";
import PrintPage from "../../Print/PrintPage";
import ReportDetail from "../ReportDetail";
import TextArea from "antd/lib/input/TextArea";
import ReportStaffInfo from "../ReportStaffInfo";
import ReportSummary from "../ReportSummary";

// styles
import classes from "./reportGenerator.module.css";

// types
import { FORM_TYPE } from "../../../types/app.types";
import { formatNumber } from "../../../utils/helper";

interface Props {
    view: FORM_TYPE;
}

const { RangePicker } = DatePicker;

const ReportGenerator = ({ view }: Props) => {
    const { data, loading, handleGenerateReport, handleGetInfoForReport } =
        useGenerateReport({
            view,
        });

    const printComponentRef = useRef(null);

    const columns = [
        {
            title: "M",
            dataIndex: "id",
            width: 50,
        },
        {
            title: "Ngy",
            dataIndex: "date",
            width: 100,
        },
        {
            title: "Tng thu",
            dataIndex: "totalRevenue",
            width: 100,
            render: (value: number) => {
                return <p>{formatNumber(value)} VND</p>;
            },
        },
        {
            title: "Tng chi",
            dataIndex: "totalExpense",
            width: 100,
            render: (value: number) => {
                return <p>{formatNumber(value)} VND</p>;
            },
        },
        {
            title: "Li nhun",
            dataIndex: "profit",
            width: 100,
            render: (value: number) => {
                return <p>{formatNumber(value)} VND</p>;
            },
        },
    ];

    return (
        <div className={classes.root}>
            {view !== "VIEW" && (
                <header className={classes.header}>
                    <Form layout="inline" onFinish={handleGetInfoForReport}>
                        <Form.Item
                            label="To bo co theo thng"
                            name="range"
                            rules={[
                                {
                                    required: true,
                                    message:
                                        "Xin hy chn thng  xem bo co",
                                },
                            ]}
                        >
                            <RangePicker />
                        </Form.Item>
                        <Form.Item>
                            <Button type="primary" htmlType="submit">
                                To bo co
                            </Button>
                        </Form.Item>
                    </Form>
                </header>
            )}

            <section className={classes.main}>
                {(data && (data.revenue !== 0 || data.expense !== 0) && (
                    <Table
                        columns={columns}
                        dataSource={data?.row}
                        loading={loading}
                        pagination={false}
                        scroll={{ y: 300 }}
                        bordered
                        title={() => (
                            <strong>
                                Bo co thng k t ngy{" "}
                                {new Date(
                                    (data && data.dateFrom) ||
                                        (data && data.reportFrom)
                                ).toLocaleDateString()}
                                n{" "}
                                {new Date(
                                    (data && data.dateTo) ||
                                        (data && data.reportTo)
                                ).toLocaleDateString()}
                            </strong>
                        )}
                        footer={() => {
                            if (!data) return <p>Loading...</p>;
                            return (
                                <React.Fragment>
                                    <div className={classes.summary}>
                                        <ReportSummary
                                            data={{
                                                budget:
                                                    (data && data.budget) || 0,
                                                expense: data.expense || 0,
                                                profit: data.profit || 0,
                                                revenue: data.revenue || 0,
                                            }}
                                        />

                                        <Form
                                            onFinish={handleGenerateReport}
                                            key={Math.random()}
                                        >
                                            <Form.Item
                                                label="M t bo co"
                                                name="description"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message:
                                                            "Xin hy nhp m t cho bo co",
                                                    },
                                                ]}
                                            >
                                                <TextArea
                                                    disabled={view === "VIEW"}
                                                    defaultValue={
                                                        data.description
                                                    }
                                                />
                                            </Form.Item>
                                            {view === "VIEW" && (
                                                <ReportStaffInfo
                                                    data={data.staff}
                                                />
                                            )}
                                            <Form.Item>
                                                {view !== "VIEW" && (
                                                    <Button
                                                        type="primary"
                                                        htmlType="submit"
                                                    >
                                                        Lu bo co
                                                    </Button>
                                                )}
                                                {view === "VIEW" && (
                                                    <React.Fragment>
                                                        <div
                                                            className={
                                                                classes.print
                                                            }
                                                        >
                                                            <ReactToPrint
                                                                trigger={() => (
                                                                    <Button
                                                                        type="primary"
                                                                        className={
                                                                            classes.pdfBtn
                                                                        }
                                                                    >
                                                                        Xut PDF
                                                                    </Button>
                                                                )}
                                                                content={() =>
                                                                    printComponentRef.current
                                                                }
                                                            />
                                                            <PrintPage
                                                                ref={
                                                                    printComponentRef
                                                                }
                                                            >
                                                                <ReportDetail
                                                                    data={data}
                                                                />
                                                            </PrintPage>
                                                        </div>
                                                    </React.Fragment>
                                                )}
                                            </Form.Item>
                                        </Form>
                                    </div>
                                </React.Fragment>
                            );
                        }}
                    />
                )) || <Empty description="Khng th truy xut data" />}
            </section>
        </div>
    );
};

export default ReportGenerator;
