import { message, Modal } from "antd";
import { useEffect, useState } from "react";
import { iBudget } from "../../types/budget.types";
import { useBudget } from "./useBudget";

/**
 * A [React Hook]{@link https://reactjs.org/docs/hooks-intro.html}
 * that provides budget list logic
 * @kind function.
 *
 *
 * @return {{
 * data: iBudget[]
 * }}
 *
 * */
const useBudgetList = () => {
    const [data, setData] = useState<iBudget[]>([]);
    const [loading, setLoading] = useState<boolean>(false);
    const [totalNumber, setTotalNumber] = useState<number>(0);

    const { fetchBudgets, deleteBudget } = useBudget();

    useEffect(() => {
        handleFetchBudgets();
    }, []);

    const handleFetchBudgets = async () => {
        setLoading(true);
        const data = await fetchBudgets();
        setData(data.data);
        setTotalNumber(data.data.length);
        setLoading(false);
    };

    const handleSearch = () => {};

    const onDelete = (budgetID: number) => {
        Modal.confirm({
            title: "Xa ngn sch ny ny?",
            content: `Bn c chc mun xa ngn sch ny khng? 
                Tt c cc bo co lin quan n ngn sch ny cng s b xa`,
            okText: "Xc nhn xa",
            cancelText: "Hy",
            onOk: async () => {
                const data = await deleteBudget(budgetID);
                await handleFetchBudgets();
                if (data.status === 200) {
                    message.success("Xa ngn sch thnh cng");
                } else {
                    message.error(" xy ra li. Xin th li sau");
                }
            },
        });
    };

    return {
        data,
        loading,
        totalNumber,

        onDelete,
        handleSearch,
    };
};

export { useBudgetList };
