import React from "react";

// talons
import { useRevenueForm } from "../../../talons/Revenue/useRevenueForm";

// utils
import mergeClasses from "../../../utils/mergeClasses";

// components
import { DatePicker, Form, Input, InputNumber } from "antd";

// styles
import defaultClasses from "./revenueForm.module.css";

// types'
import { FORM_TYPE } from "../../../types/app.types";

interface Props {
    classes?: object;
    view: FORM_TYPE;
}

const RevenueForm = ({ classes: propsClasses, view }: Props) => {
    const classes = mergeClasses(defaultClasses, propsClasses);

    const { form, revenue, handleCancel, onChange, onSubmit } = useRevenueForm({
        view,
    });

    let title = null;
    let buttonText = null;

    switch (view) {
        case "ADD":
            title = "Thm thng tin doanh thu";
            buttonText = "Thm";
            break;
        case "EDIT":
            title = "Sa thng tin doanh thu";
            buttonText = "Lu thng tin";
            break;
        case "VIEW":
            title = "Thng tin chi tit doanh thu";
            buttonText = "Sa";
            break;
        default:
            title = "Thng tin doanh thu";
            buttonText = "";
    }

    return (
        <div className={classes.root}>
            <h2 className={classes.title}>{title}</h2>

            <Form
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 14 }}
                layout="horizontal"
                onFinish={onSubmit}
                onValuesChange={onChange}
                form={form}
                initialValues={revenue || {}}
                key={Math.random()}
            >
                <Form.Item label="Ngy" name="createdAt">
                    <DatePicker />
                </Form.Item>
                <Form.Item
                    label="Tn"
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp tn cho phiu thu",
                        },
                    ]}
                >
                    <Input disabled={view === "VIEW"} />
                </Form.Item>
                <Form.Item
                    label="M t"
                    name="description"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp m t cho phiu thu",
                        },
                    ]}
                >
                    <Input disabled={view === "VIEW"} />
                </Form.Item>
                <Form.Item
                    label="S lng"
                    name="quantity"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp s lng",
                        },
                    ]}
                >
                    <InputNumber
                        disabled={view === "VIEW"}
                        formatter={(value) =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={(value: any) =>
                            (value && value.replace(/\$\s?|(,*)/g, "")) || ""
                        }
                        min={0}
                    />
                </Form.Item>{" "}
                <Form.Item
                    label="n gi"
                    name="priceUnit"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp n gi",
                        },
                    ]}
                >
                    <InputNumber
                        disabled={view === "VIEW"}
                        formatter={(value) =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={(value: any) =>
                            (value && value.replace(/\$\s?|(,*)/g, "")) || ""
                        }
                        min={0}
                    />
                </Form.Item>
                <Form.Item
                    label="Tng tin"
                    name="total"
                    rules={[
                        {
                            required: true,
                            message: "Please input name for revenue",
                        },
                    ]}
                >
                    <InputNumber
                        disabled
                        formatter={(value) =>
                            `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                        parser={(value: any) =>
                            (value && value.replace(/\$\s?|(,*)/g, "")) || ""
                        }
                        min={0}
                    />
                </Form.Item>
                <Form.Item
                    label="S ti khon ngn hng"
                    name="bankAccountNumber"
                    rules={[
                        {
                            required: true,
                            message: "Xin hy nhp s ti khon ngn hng",
                        },
                    ]}
                >
                    <Input disabled={view === "VIEW"} />
                </Form.Item>
                <div className={classes.btnGroup}>
                    <button type="submit" className={classes.btn}>
                        {buttonText}
                    </button>
                    <button className={classes.btn} onClick={handleCancel}>
                        Hy
                    </button>
                </div>
            </Form>
        </div>
    );
};

export default RevenueForm;
